@use 'base/variables' as v;
@use 'base/mixins' as m;

.iconos {

    margin-top: 5rem;

    .listado-iconos {

        @include m.tablet{
            @include m.grid(3, 2rem);
        }

        li {
            display: flex;
            flex-direction: column;
            align-items: center;
            img {
                width: 12rem;
            }
            p {
                font-family: v.$fuenteCursiva;
                color: v.$primario;
                font-size: 3.2rem;
            }
        }
    }
}