@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$primario;

    .contenido-footer {
        padding: 5rem;

        @include m.tablet{
            @include m.grid(3, 2rem);
        }
    }
}

.footer-box {
    font-family: v.$fuenteCursiva;
    margin-bottom: v.$separacion;

    @include m.tablet{
        margin-bottom: 0;
    }
    h3 {
        font-family: v.$fuenteCursiva;
        margin: 0 0 1rem 0;
        font-size: 2.4rem;
        color: v.$secundario;
    }

    text-align: center;

    p, a {
        font-size: 2rem;
        margin: 0;
    }
}

.copyright {
    text-align: center;
    margin: 0;
    padding: 2rem;
    color: v.$blanco;
}